/* eslint-disable import/no-anonymous-default-export */
export default {
    namespace: 'cart',
    state: {
        shopCart: [],
        isEject: false,
    },
    reducers: {
        // æ·»åŠ å•†å“è‡³è´­ç‰©è½¦
        addToCart(state, {payload: good}) {
            // console.log('addToCart', good);
            let tempCart = state.shopCart
            let sign = false
            for (let v of tempCart) {
                if (v.id === good.id) {
                    v.quantity++
                    sign = true
                    break
                }
            }
            if (!sign) {
                good.quantity = 1
                tempCart.push(good)
            }
            state.shopCart = [...tempCart]
            if (!state.isEject) state.isEject = true
            return {
                ...state,
            }
        },
        // æ‰“å¼€è´­ç‰©è½¦
        openShop(state) {
            if (!state.isEject) state.isEject = true
            return { ...state }
        },
        // å…³é—­è´­ç‰©è½¦
        closeShop(state) {
            if (state.isEject) state.isEject = false
            return { ...state }
        },
        // åˆ é™¤è´­ç‰©è½¦ä¸­çš„å•†å“
        deleteCart(state, {payload: id}) {
            // console.log('deleteCart', id);
            let tempCart = state.shopCart
            for (let i in tempCart) {
                if (tempCart[i].id === id) {
                    tempCart.splice(i, 1)
                    break
                }
            }
            if (tempCart.length === 0) state.isEject = false
            state.shopCart = tempCart.slice()
            return {
                ...state
            }
        },
        // å¢åŠ ä¸€ä¸ªå•†å“
        addOneGood(state, {payload: id}) {
            let tempCart = state.shopCart
            for (let v of tempCart) {
                if (v.id === id) {
                    v.quantity++
                    break
                }
            }
            state.shopCart = tempCart.slice()
            return {
                ...state
            }
        },
        // å‡å°‘ä¸€ä¸ªå•†å“
        subtrackGood(state, {payload: id}) {
            let tempCart = state.shopCart
            for (let v of tempCart) {
                if (v.id === id) {
                    v.quantity--
                    break
                }
            }
            state.shopCart = tempCart.slice()
            return {
                ...state
            }
        },
        // æ¸…ç©ºè´­ç‰©è½¦
        clearShopCart(state) {
            state.shopCart = []
            state.isEject = false
            return {
                ...state
            }
        },
        // è·å–æœ¬åœ°å­˜å‚¨
        getShopCart(state) {
            state.shopCart = JSON.parse(localStorage.getItem('shopCart'))
            return { ...state }
        }
    }
}